.form-group-reverse {
  display: flex;
  flex-direction: column-reverse;
}

.form-label {
  font-size: 12px;
  font-weight: 500;
  color: #767676;
}
.form-label .invalid {
  display: none;
}

.form-control {
  padding: 15px;
  height: 48px;
  border-radius: 6px;
  border: 2px solid #D2D2D2;
}
.form-control:focus {
  box-shadow: none;
}

.was-validated .form-control:valid ~ label.form-label .valid,
.was-validated .form-control.is-valid ~ label.form-label .valid {
  display: block;
}
.was-validated .form-control:valid ~ label.form-label .invalid,
.was-validated .form-control.is-valid ~ label.form-label .invalid {
  display: none;
}
.was-validated .form-control:invalid ~ label.form-label,
.was-validated .form-control.is-invalid ~ label.form-label {
  color: #FF0000;
}
.was-validated .form-control:invalid ~ label.form-label .valid,
.was-validated .form-control.is-invalid ~ label.form-label .valid {
  display: none;
}
.was-validated .form-control:invalid ~ label.form-label .invalid,
.was-validated .form-control.is-invalid ~ label.form-label .invalid {
  display: block;
}
.was-validated .form-control:valid,
.was-validated .form-control.is-valid {
  border-color: #D2D2D2;
}
.was-validated .form-control:valid:focus,
.was-validated .form-control.is-valid:focus {
  border-color: #80bdff;
  box-shadow: none;
}
.was-validated .form-control:invalid,
.was-validated .form-control.is-invalid {
  border-color: #FF0000;
}
.was-validated .form-control:invalid:focus,
.was-validated .form-control.is-invalid:focus {
  border-color: #FF0000;
  box-shadow: none;
}
.was-validated .custom-control-input:valid ~ .custom-control-label,
.was-validated .custom-control-input.is-valid ~ .custom-control-label {
  color: #767676;
}
.was-validated .custom-control-input:invalid ~ .custom-control-label,
.was-validated .custom-control-input.is-invalid ~ .custom-control-label {
  color: #FF0000;
}
.was-validated .custom-control-input:invalid:not(:checked) ~ .custom-control-label::after,
.was-validated .custom-control-input.is-invalid:not(:checked) ~ .custom-control-label::after {
  border-color: #FF0000;
}
.was-validated .custom-control-input:valid:checked ~ .custom-control-label::before,
.was-validated .custom-control-input.is-valid:checked ~ .custom-control-label::before {
  background-color: #6165D7;
}
.was-validated .form-control.no-icon:valid,
.was-validated .form-control.no-icon.is-valid {
  background-image: none;
}
.was-validated .form-control.person-icon.is-valid,
.was-validated .form-control.person-icon:valid {
  background-image: url(../../assets/img/svg/icons/person-icon.svg);
}
.was-validated .form-control.email-icon.is-valid,
.was-validated .form-control.email-icon:valid {
  background-image: url(../../assets/img/svg/icons/evenlope-blue.svg);
}
.was-validated .form-control.phone-icon.is-valid,
.was-validated .form-control.phone-icon:valid {
  background-image: url(../../assets/img/svg/icons/phone-blue-2.svg);
}
.was-validated .form-control.person-icon.is-invalid,
.was-validated .form-control.person-icon:invalid {
  background-image: url(../../assets/img/svg/icons/person-icon.svg);
}
.was-validated .form-control.email-icon.is-invalid,
.was-validated .form-control.email-icon:invalid {
  background-image: url(../../assets/img/svg/icons/evenlope-blue.svg);
}
.was-validated .form-control.phone-icon.is-invalid,
.was-validated .form-control.phone-icon:invalid {
  background-image: url(../../assets/img/svg/icons/phone-blue-2.svg);
}

.custom-control {
  padding-left: 0;
}

.custom-control-input:checked ~ .custom-control-label::before {
  box-shadow: none;
  background-color: #6165D7;
}
.custom-control-input:checked ~ .custom-control-label::after {
  background-image: none;
}

.custom-control-input:focus ~ .custom-control-label::before {
  box-shadow: none;
}

.custom-control-label {
  padding-left: 30px;
}
.custom-control-label::after, .custom-control-label::before {
  border-radius: 50%;
}
.custom-control-label::before {
  left: calc(0px + 4px);
  top: calc(3px + 4px);
  width: 8px;
  height: 8px;
  border: none;
}
.custom-control-label::after {
  left: 0;
  top: 3px;
  width: 16px;
  height: 16px;
  border: 2px solid #D2D2D2;
}

.checkbox-block .custom-control-label {
  font-size: 14px;
  color: #767676;
}

