.scroller {
  position: fixed;
  right: 70px;
  bottom: 200px;
}
.scroller .scroller-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.scroller .scroller-wrap .chats-links-wrap {
  overflow: hidden;
}
.scroller .scroller-wrap .chats-links {
  display: flex;
  flex-direction: column;
  transform: translateY(100%);
  transition: all 0.2s ease-in-out;
}
.scroller .scroller-wrap .chats-links a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 27px;
  height: 27px;
  background-color: #E7EBFC;
  border-radius: 50%;
  margin-bottom: 10px;
  transition: all 0.2s ease-in-out;
}
.scroller .scroller-wrap .chats-links a:last-child {
  margin-bottom: 0;
}
.scroller .scroller-wrap .chats-links.show {
  transform: translateY(0);
  margin-bottom: 10px;
}
.scroller #show-chats {
  width: 54px;
  height: 54px;
  background-color: #FFCC00;
  border-radius: 50%;
  transition: all 0.2s ease-in-out;
}

@media (max-width: 991px) {
  .scroller {
    display: none;
  }
}
button.on-top {
  position: fixed;
  right: 70px;
  bottom: 110px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 54px;
  height: 54px;
  background-color: #FFCC00;
  padding: 0;
  border-radius: 50%;
  transition: all 0.2s ease-in-out;
  opacity: 0;
  visibility: hidden;
}
button.on-top svg {
  transform: rotate(180deg);
}

button.on-top.show {
  opacity: 1;
  visibility: visible;
}

@media (max-width: 991px) {
  button.on-top {
    right: 25px;
  }
}
@media (max-width: 767px) {
  button.on-top {
    width: 40px;
    height: 40px;
  }
  button.on-top svg {
    width: 18px;
  }
}
.modal-content {
