body {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  color: #1A1A1A;
}

body.no-scroll {
  overflow: hidden;
  position: fixed;
}

main {
  overflow: hidden;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

section {
  padding-bottom: 150px;
}
section .section-title {
  margin-bottom: 80px;
}

a {
  color: #1A1A1A;
}
a:hover {
  color: #1A1A1A;
}

a.link {
  transition: all 0.2s ease-in-out;
}
a.link .svg-icon {
  transition: all 0.2s ease-in-out;
}
a.link:hover {
  color: #FFCC00;
}
a.link:hover .svg-icon {
  fill: #FFCC00;
}

.dropdown-menu {
  margin: 0;
  padding: 25px 0 0;
  border: none;
  background-color: transparent;
}

.dropdown-menu-wrap {
  padding: 10px 0 20px;
  position: relative;
  background-color: rgba(255, 255, 255, 0.94);
  border: 1px solid #ECEDED;
  border-radius: 2px;
}
.dropdown-menu-wrap::before {
  content: "";
  width: 20px;
  height: 20px;
  background-color: rgba(255, 255, 255, 0.94);
  position: absolute;
  top: -10px;
  left: 65px;
  border-top-color: #ECEDED;
  border-top-style: solid;
  border-top-width: 1px;
  border-left-color: #ECEDED;
  border-left-style: solid;
  border-left-width: 1px;
  transform: rotate(45deg);
}

.dropdown-item {
  position: relative;
  padding: 0 25px;
  line-height: 2;
}

.spacer {
  flex-grow: 1;
}

@media (max-width: 991px) {
  section {
    padding-bottom: 100px;
  }
  section .section-title {
    margin-bottom: 60px;
  }
}
@media (max-width: 767px) {
  section {
    padding-bottom: 80px;
  }
  section .section-title {
    margin-bottom: 50px;
  }
}